import {React, useState} from 'react'
import {GoogleMap, InfoWindow, Marker, useJsApiLoader} from '@react-google-maps/api'

const Map = () => {

    const containerStyle = {
        width: '100%',
        height: '100%',
        border: '5px solid #962125',
        bordeRadius: '20px',
        zIndex: '1'
    };

    const [nameMarker, setNameMarker] = useState("");
    const [center, setCenter] = useState({lat: -33.376152, lng: -58.377889})
    const [markerPosition, setMarkerPosition] = useState(center);
    const [infoWindowOpen, setInfoWindowOpen] = useState(false);

    const onMarkerOpen = (name) =>{
        setInfoWindowOpen(!infoWindowOpen)
        setNameMarker(name)
        return;
    }

    const onCloseInfoWindow = () =>{
        setInfoWindowOpen(false);
    } 

    const markers = [
        {
            lat: -32.219257125292934,   
            lng: -58.13663661561708,
            url: "https://maps.app.goo.gl/1kJhmvJpUgCkaDxf9",
            name: 'Vórtice',
            info: "3 de Febrero 37, Colón, Entre Ríos",
            id: 1
        },
        {
            lat: -32.221719159231874,  
            lng: -58.13795894473787,
            url: "https://maps.app.goo.gl/8Ln61tLPtsJYCmPs6",
            name: "El bodegón de la fiambreria",
            info: "Gral. Urquiza 269, Colón, Entre Ríos",
            id: 2
        },
        {
            lat: -32.22288548196439,    
            lng: -58.13954681234572,
            url: "https://maps.app.goo.gl/NQKM4GB6r1w4zg9N7",
            name: "Don Juan - Quesos y Fiambres",
            info: "Gral. Urquiza 375, Colón, Entre Ríos",
            id: 3
        },
        {
            lat: -34.60652502754125,    
            lng: -58.44960255526251,
            url: "https://maps.app.goo.gl/jQx44RUhp15GxZ4Y8",
            name: "MEDIA COPA GARCÍA",
            info: "Av. S. Martín 1431, C1416CRC Ramos Mejía, Buenos Aires",
            id: 4
        },
        {
            lat: -34.859446226776946,   
            lng: -58.39020235471152,
            url: "https://maps.app.goo.gl/L78JD2w2Hcx29jiHA",
            name: "Serbeers",
            info: "Av. Hipólito Yrigoyen 18281, Longchamps, Provincia de Buenos Aires",
            id: 5
        },
        {
            lat: -32.215972335564445,    
            lng: -58.13569155666779,
            url: "https://maps.app.goo.gl/ywG1T6StzYgNHUR7A",
            name: "Galpon Don Enrique",
            info: "Av. Gdor. Quiros 100-130, Colón, Entre Ríos",
            id: 6
        },
        {
            lat: -32.22154548790819,   
            lng: -58.13856688488686,
            url: "https://maps.app.goo.gl/45RpjuVLqWw7efMX7",
            name: "Trébol Artesanías - América Shopping",
            info: "12 de Abril 287 Piso 1 Local 1, E3280 Colón, Entre Ríos",
            id: 7
        },
        {
            lat: -32.21965755243853,   
            lng: -58.13589540429761,
            url: "https://maps.app.goo.gl/cK9oXfHCPvWUgig4A",
            name: "Regionales El Gauchito",
            info: "12 de Abril 136, E3280 Colón, Entre Ríos",
            id: 8
        },
        {
            lat: -32.21606310598743,   
            lng: -58.136260184694,
            url: "https://maps.app.goo.gl/S6E81nyZxNZ4ZHzq8",
            name: "Regionales Del puerto",
            info: "3280, Colón, Entre Ríos",
            id: 9
        },
        {
            lat: -32.220796671916446,   
            lng: -58.13950029323626,
            url: "https://maps.app.goo.gl/G3Ch7jJQkZE78TxK8",
            name: "Yuca Vinoteca Colón",
            info: "Yuca Vinoteca, Av San Martín 297, E3280 Colón, Entre Ríos",
            id: 10
        },
        {
            lat: -32.215704561236855,  
            lng: -58.13646939698017,
            url: "https://maps.app.goo.gl/9qh8S1g4dRaXgrK59",
            name: "Sabores de Campo",
            info: "Gouchon 20 48, Colón, Entre Ríos",
            id: 11
        },
        {
            lat: -32.22047445233558,  
            lng: -58.13925889444453,
            url: "https://maps.app.goo.gl/kjmNQREDCvxD3PUF8",
            name: "Alfajores Artesanales Aquellos 530" ,
            info: "Av San Martín 277, Colón, Entre Ríos",
            id: 12
        },
        {
            lat: -32.209904109212175,   
            lng: -58.220079212322084,
            url: "https://maps.app.goo.gl/naHBBjADmr8v7fDs9",
            name: "La Cerveceria",
            info: "Centenario 1361, San José, Entre Ríos",
            id: 13
        },
        {
            lat: -32.1962502550525,  
            lng: -58.17064073968436,
            url: "https://maps.app.goo.gl/rokCLYWDbZWPnKgV7",
            name: "El Paseo Del Queso",
            info: "Entre Ríos",
            id: 14
        },


    ];

    const { isLoaded } = useJsApiLoader({
        id: 'google-map-script',
        googleMapsApiKey: "AIzaSyAgt5l9LufHZ7Yp8xVpQ_Rr0L9ZGfgp6gs"
    });

    

  return isLoaded ? (
    <GoogleMap
        mapContainerStyle={containerStyle}
        center={center}
        zoom={7}
        options={{styles: [{featureType: 'poi',elementType: 'labels.icon',stylers: [{ visibility: 'off' }],},]}}
      >
        {markers.map((marker) => 
            <Marker key={marker.id} draggable={false} position={{lat: marker.lat, lng: marker.lng}} onClick={() => {onMarkerOpen(marker.name)}}>
            {infoWindowOpen && (
                nameMarker === marker.name ? <InfoWindow name={marker.name} position={markerPosition} onCloseClick={() => {
                    onCloseInfoWindow()
                    const newCenter = {
                        lat: marker.lat,
                        lng: marker.lng
                    }
                    setMarkerPosition(newCenter);
                    setCenter(newCenter);
                }}>
                  <div>
                    <h4>{marker.name}</h4>
                    <p>{marker.info}</p>
                    <a href={marker.url} target='_blank' rel="noreferrer" className='text-indigo-600'>Ver en google maps</a>
                  </div>
                </InfoWindow> : <></>
              )}
            </Marker>
        )}
        { /* Child components, such as markers, info windows, etc. */ }
      </GoogleMap>

  ) : <></>
}

export default Map