import React from 'react'
import logo from '../assets/imgs/logo.png'


const Home = () => {
  return (
    <section id='home' className="h-[100vh] w-[100%] flex flex-col items-center justify-center bg-homeMobile-pattern md:bg-home-pattern bg-center bg-cover">
      <div className='flex flex-col items-center justify-around md:flex-row w-[85%] md:w-[100%] gap-10'>
        <img data-aos="fade-right" data-aos-delay="500" data-aos-duration="500" className='h-[6.5rem] md:h-[15rem]' src={logo} alt="" />
        <h1 data-aos="fade-left" data-aos-delay="500" data-aos-duration="500" className={`text-white text-[.8rem] md:text-[1.5rem] text-center font-blatan uppercase tracking-[.3rem]`}>Bienvenidos al lado salvaje <br/> del fernet</h1>
      </div>
    </section>

  )
}

export default Home