import React from 'react'
import Map from './Map'

const Venta = () => {
  return (
    <section id='dondePardalis' className='h-[100vh] bg-ventasMobile-pattern bg-cover bg-center flex flex-col lg:flex-row-reverse items-center justify-around relative z-[0]'>
        <div className='flex flex-col items-center md:w-[40%] lg:w-[50%]'>
          <h4 className='text-[2.5rem] 2xl:text-[3.5rem] text-[#962125] font-cristone'>Puntos de venta</h4>
          <p className='text-white font-bahnschrift lg:text-[1.5rem] text-center'>Tu Pardalis está más cerca de lo que pensás ¡buscalo!</p>
        </div>
        <div data-aos="fade-right" data-aos-delay="300" className='w-[90%] h-[50%] md:w-[50%] md:pl-10 lg:w-[50%] lg:h-[50%] relative z-[0]'>
          <Map/>
        </div>
    </section>
  )
}

export default Venta